.container {
  padding: 3rem;
  background-color: rgb(68, 68, 68);
  height: calc(100vh - 5rem);
}

.section {
  margin: 3rem 0;
}

.title {
  font-size: 1.7rem;
  font-weight: bold;
  text-align: right;
  color: white;
}

.section-content {
  margin-top: 1rem;
}

.row {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  margin: 0 1rem;
  font-size: 1.4rem;
  font-weight: bold;
  margin: 0.8rem;
  color: white;
}

.back-button {
  text-align: right;
  background-color: white;
  border-radius: 10px;
  font-size: 1.6rem;
  padding: 1rem;
  text-decoration: none;
  width: 100%;
  display: block;
}
